body {
    background-image: url('./images/banner-10-Preview-1.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed; 
   font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

 

}

h2{
   font-size: medium;
    color: red;
    
}

p {
    text-align: center;
    font-weight: bold;
    color: rgb(0, 255, 179);
    font-size: 20px;
    padding-bottom: 30px;
}

.game-buttons {
    display: flex;
    justify-content: center;
    margin-top: 100px;
   

}
.game-buttons img {
    justify-content: center;
    height: 80px;
    width: 80px;
    background-color: rgb(15, 7, 119);
   
    
}

.game-buttons button {


    padding: 5px;
    background: white;
   
   
}
.choose-com {
      text-align: left;
      font-size: medium;
      margin-bottom: 80px;
      color: cyan;
      padding-bottom: 100px;


}
.result {
    font-size: 25px;
    color: chartreuse;
    text-align: left;
   
}

.game-history {
    margin-top: 20px;
    text-align: left;
    color: cyan;
}

